<template>
  <v-container
  >
    <v-row
        no-gutters
    >
      <v-card
          class="white pa-2 mx-0"
          tile
      >
        <v-card-title>Vergleich mit vorhandenen Konten</v-card-title>
        <v-card-text>
          <v-row>
<!--            <v-col cols="3">
              <v-btn elevation="2" v-on:click="updateAccounts">Alle Konten aktualisieren</v-btn>
            </v-col> -->
            <v-col cols="4">
              <v-btn elevation="2" v-on:click="searchDouble">Doppelte suchen</v-btn>
            </v-col>
            <v-col cols="4">
              <v-btn elevation="2" v-on:click="updateSelected">Selektierte Konten aktualisieren</v-btn>
            </v-col>
            <v-col cols="4">
              <v-btn elevation="2" v-on:click="updateNextcloud">Update NextCloud</v-btn>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-row>
    <v-card class="mx-auto mt-10" outlined>
      <v-card-title v-if="susChange.length==0">Zu ändernde Konten</v-card-title>
      <v-card-title v-if="susChange.length!=0">{{susChange.length}} zu ändernde Konten</v-card-title>
        <VueTabulator
            ref="tabChange"
            class="table-striped table-sm overflow-y-auto"
            v-model="susChange"
            :integration="{ updateStrategy: 'UPDATE' }"
            :options="configuration"
            @cell-edited="cellEditedChanged"
        />
      </v-card>
    <v-card class="mx-auto mt-10" outlined>
      <v-card-title v-if="susNew==0">Neu anzulegende SuS-Konten</v-card-title>
      <v-card-title v-if="susNew!=0">{{susNew.length}} neu anzulegende SuS-Konten</v-card-title>
      <VueTabulator
          ref="tabNew"
          class="table-striped table-sm overflow-y-auto"
          v-model="susNew"
          :integration="{ updateStrategy: 'UPDATE' }"
          :options="configuration"
          @cell-edited="cellEditedNew"
      />
    </v-card>
    <v-card class="mx-auto mt-10" outlined>
      <v-card-title v-if="susDelete==0">Zu löschende SuS-Konten</v-card-title>
      <v-card-title v-if="susDelete!=0">{{susDelete.length}} zu löschende SuS-Konten</v-card-title>
      <VueTabulator
          ref="tabDelete"
          class="table-striped table-sm overflow-y-auto"
          v-model="susDelete"
          :integration="{ updateStrategy: 'UPDATE' }"
          :options="configuration"
          @cell-edited="cellEditedDel"
      />
    </v-card>
  </v-container>
</template>

<script>
import {mapGetters} from "vuex";
import TransferService from '../services/transfer.service';
import * as Const from '../config';


export default {
  name: 'LoadFile',

  data () {
    return {
      choosenFile: null,
      search: '',

      susChange: [],
      susNew: [],
      susDelete: [],
      accDouble: [],

      configuration: {
        resizableRows:false,
        maxHeight:"40vH",
        index:'id',
        layout: 'fitDataStretch',
        columns: [],
//        ajaxURL: '/test',
//        ajaxRequestFunc:queryService,
      }

    }
  },

  mounted() {
    if (!this.currentUser) {
      this.$router.push('/login');
    }

    this.loadConfig();
    this.checkAccounts();
  },

  computed: {
    ...mapGetters( 'auth', ['currentUser']),
  },

  methods: {
    loadConfig() {
      this.configuration.columns = [
        {formatter:"rowSelection", titleFormatter:"rowSelection", hozAlign:"center", headerSort:false,
          cellClick:function(e, cell){
            cell.getRow().toggleSelect();
          }},
        {title: 'Klasse', field: 'klasse', width: 100, sorter: 'string', headerFilter:'input',
          formatter: function(cell, formatterParams, onRendered){
            return formatChange(cell, formatterParams, onRendered);
          }, formatterParams: {col:'bklasse'}
        },
        {title: 'Name', field: 'anzeigeName', width: 200, sorter: 'string', editor: 'input', headerFilter:'input',
          formatter: function(cell, formatterParams, onRendered){
            return formatChange(cell, formatterParams, onRendered);
          }, formatterParams: {col:'banzeigeName'}
        },
        {title: 'Login', field: 'loginName', width: 100, sorter: 'string', editor: 'input', headerFilter:'input',
          formatter: function(cell, formatterParams, onRendered){
            return formatChange(cell, formatterParams, onRendered);
          }, formatterParams: {col:'bloginName'}
        },
        {title: 'Nachname', field: 'nachname', width: 150, sorter: 'string', headerFilter:'input',
          formatter: function(cell, formatterParams, onRendered){
            return formatChange(cell, formatterParams, onRendered);
          }, formatterParams: {col:'bnachname'}
        },
        {title: 'Vorname', field: 'vorname', width: 150, sorter: 'string', headerFilter:'input',
          formatter: function(cell, formatterParams, onRendered){
            return formatChange(cell, formatterParams, onRendered);
          }, formatterParams: {col:'bvorname'}
        },
        {title: 'GebDatum', field: 'geburtstag', sorter: 'string', headerFilter:'input',
          formatter: function(cell, formatterParams, onRendered){
            return formatChange(cell, formatterParams, onRendered);
          }, formatterParams: {col:'bgeburtstag'}
        },
        {title: 'EMail', field: 'email', width: 200, sorter: 'string', headerFilter:'input',
          formatter: function(cell, formatterParams, onRendered){
            return formatChange(cell, formatterParams, onRendered);
          }, formatterParams: {col:'bemail'}
        },
        {title: 'ID', field: 'id', width: 200, sorter: 'string', headerFilter:'input'},
      ];
//      this.configuration.ajaxURL = ACCOUNT_API + 'read';
    },

    loadLDAP(){
      TransferService.get(Const.LOAD_EXT_ACCOUNTS);
    },

    checkAccounts(){
//      TransferService.getData(ACCOUNT_API + 'compacc');
      let that = this;
      this.$store.dispatch('domain/readAccState').then(function(){
        that.susChange = that.$store.getters["domain/getToChange"];
        that.$refs.tabChange.getInstance().replaceData(that.susChange);
        that.susNew = that.$store.getters["domain/getToNew"];
        that.$refs.tabNew.getInstance().replaceData(that.susNew);
        that.susDelete = that.$store.getters["domain/getToDelete"];
        that.$refs.tabDelete.getInstance().replaceData(that.susDelete);
      });
    },

    searchDouble(){
      let that = this;
      this.$store.dispatch('domain/searchDouble').then(function(){
        that.accDouble = that.$store.getters["domain/getDouble"];
      });
    },

    updateAccounts(){
      TransferService.get(Const.UPDATE_ALL_ACCOUNTS);
      this.checkAccounts();
    },

    updateSelected(){
      var cData = this.$refs.tabChange.getInstance().getData('selected');
      var nData = this.$refs.tabNew.getInstance().getData('selected');
      var dData = this.$refs.tabDelete.getInstance().getData('selected');

      var data = {
        unchanged: cData.length+nData.length+dData.length,
        toCOld: [],
        toChange: cData,
        toCreate: nData,
        toDelete: dData
      };

      TransferService.post(Const.UPDATE_SELECTED_ACCOUNTS, data);

      this.checkAccounts();
    },

    updateNextcloud(){
      TransferService.get(Const.UPDATE_NEXTCLOUD);
    },

    cellEdited(cell, path){
      let d = cell._cell.row.data;
      console.log("New Row data: " + d);
      TransferService.postRow(path, d);
    },

    cellEditedChanged(cell) {
      this.cellEdited(cell, Const.UPDATE_ROW_CHG);
    },

    cellEditedNew(cell) {
      this.cellEdited(cell, Const.UPDATE_ROW_NEW);
    },

    cellEditedDel(cell) {
      this.cellEdited(cell, Const.UPDATE_ROW_DEL);
    },
  },
}
// eslint-disable-next-line no-unused-vars
function formatChange(cell, formatterParams, onRendered){
  var data = cell.getData();
  if(data[formatterParams.col]){
    cell.getElement().style.color = "#c62020";
  }
  return cell.getValue();
}
</script>


<style lang='scss'>
//$textSize:0.9rem;
//@import "~vue-tabulator/dist/scss/bootstrap/tabulator_bootstrap4";
//@import "~vue-tabulator/dist/scss/tabulator";
@import "~tabulator-tables/dist/css/tabulator";
</style>